.service5 {
  padding: 30px 7%;

  .service5-container {
    display: grid;
    grid-template-columns: 50% 50%;

    .right {
      .right-container {
        height: 100%;

        .hexagon-container {
          filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.6));
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;

          .hexagon {
            clip-path: polygon(50% 0%, 83% 12%, 100% 43%, 94% 78%, 68% 100%, 32% 100%, 6% 78%, 0% 43%, 17% 12%);
            background: url(../../assets/img/b.jpeg) center/cover;
            width: 30%;
            height: 90%;
            overflow: hidden;
            box-shadow: 0px 5px 10px 0px $blue;
            transition: 0.5s;
            &:hover{
              transform: scale(1.1);
            }
          }
        }
      }
    }

    .left {
      .left-container {
        .content {
          .title {
            text-align: left;
            margin: 20px 0 10px 7%;
            font-family: $rosario-italic;
            font-size: 2rem;
            text-transform: uppercase;
          }

          .text {
            p {
              font-family: $rosario-italic;
              font-size: 1.3rem;
              text-align: justify;
              text-indent: 40px;
            }
          }
        }
      }
    }
  }
}