.service1{
  padding: 30px  7%;
  .service1-container{
    display: grid;
    grid-template-columns: 50% 50%;
    .left{
      .left-container{
        margin-top: 90px;
        .content{
          .title {
            text-align: left;
            margin: 20px 0 10px 7%;
            font-family: $rosario-italic;
            font-size: 2rem;
            text-transform: uppercase;
          }
          .text{
            p{
              font-family: $rosario-italic;
              font-size: 1.3rem;text-align: justify;
              text-indent: 40px;
            }
          }
        }
      }
    }
    .right{
      display: flex;
      justify-content: center;
      align-items: center;
        .hexagone{
          position: relative;
          width: 346.41016px;
          visibility: hidden;
          overflow: hidden;
          -webkit-filter : drop-shadow(5px 5px 5px rgba(0,0,0,0.6));
          filter : drop-shadow(5px 5px 5px rgba(0,0,0,0.6));
          transition: 0.5s;
          
            &:hover {
              transform: scale(1.1);
            }
          
          
            &::after{
              -moz-box-sizing: content-box;
              -webkit-box-sizing: content-box;
              box-sizing: content-box;
              content: "";
              display: block;
              margin-top: 28.86751%;
              padding-bottom: 86.602%;
            }
        }
        .hexagone .hexagonemain{
          display: block;
          position: absolute;
          width: 100%;
          padding-bottom: 115.47005%;
          overflow: hidden;
          visibility: hidden;
          -webkit-transform: rotate3d(0, 0, 1, -60deg) skewY(30deg);
          -ms-transform: rotate3d(0, 0, 1, -60deg) skewY(30deg);
          transform: rotate3d(0, 0, 1, -60deg) skewY(30deg);
          z-index: 1;
        }
        .hexagone .hexagonemain:before{
          content: "";
          display: block;
          position: absolute;
          visibility: visible;
          display: block;
          width: 100%;
          //border: 1px solid $blue;
          height: 100%;
          overflow: hidden;
          background: url(../../assets/img/a.jpeg) center/contain;
          -webkit-transform: skewY(-30deg) rotate3d(0, 0, 1, 60deg);
          -ms-transform: skewY(-30deg) rotate3d(0, 0, 1, 60deg);
          transform: skewY(-30deg) rotate3d(0, 0, 1, 60deg);
        }
    }
  }
}