.section2{
  background: $grey;
  overflow: hidden;
  .section2-container{
    padding: 80px 7%;
    display: grid;
    grid-template-columns: 50% 50%;
    column-gap: 50px;
    .left{
      .left-content{
        margin-top: 20px;
        //margin-right: 90px;
        //margin-left: 40px;
        // .subtitle{
        //   font-family: $tangerine;
        //   font-size: 2rem;
        // }
        .title{
          text-transform: uppercase;
          font-family: $fondamento;
          font-weight: 500;
          font-size: 2rem;
          .first{
            text-transform: capitalize;
          }
          span{
            text-transform: lowercase;
          }
          .interro{
            font-weight: 700;
            font-size: 4rem;
            color: $blue;
          }
        }
        p{
          font-family: $rosario-italic;
          text-align: justify;
          font-size: 1.2rem;
          text-indent: 40px;
        }
        .btn{
          margin-top: 30px;
          font-family: $fondamento;
          padding: 5px 10px;
          border: 1px solid $red;
          border-radius: 5px;
          color: $blue;
          background: $vio;
          box-shadow: 0px 5px 10px 0px $blue;
          width: fit-content;
          transition: 2,5s;
          &:hover{
            transform: scale(1.09);
            transition: 0.4s;
            color: $red;
            border: 1px solid $blue;
            
          }
        }
      }
    }
    .right{
    img {
        height: 100%;
        width: 92.5%;
        border-radius: 10px;
        margin-top: 20px;
      }
  }
  }
}