.garanti {
  padding: 20px 7%;
  .garanti-container {
    display: grid;
    grid-template-columns: 50% 50%;
    .right {
      position: relative;
      width: 100%;
      .icon {
        position: absolute;
        left: 40%;
        top: 25%;
        transform: translate(-50% -25%);
      }
    }
    .left {
      .left-container {
        .top {
          .title {
            font-family: $rosario-regular;
            text-transform: uppercase;
            text-align: center;
            margin: 30px auto;
            position: relative;
            font-size: 2rem;
            &::after {
              content: '';
              position: absolute;
              left: 50%;
              top: 120%;
              transform: translate(-50%, -50%);
              width: 20%;
              height: 2px;
              background: $blue;
            }
          }
        }
        .text {
          padding-left: 20px;
          ul {
            position: relative;
            &::before {
              content: '';
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              height: 80%;
              width: 8px;
              border-radius: 10px;
              background: $red;
            }
            li {
              padding: 0 4%;
              font-family: $rosario-italic;
              font-size: 1.5rem;
              text-align: justify;
              display: flex;
              span {
                color: $red;
                font-size: 1.3rem;
                padding-right: 10px;
                margin-top: 5px;
              }
            }
          }
        }
      }
    }
  }
}