.service2{
  padding: 30px 7%;
  .service2-container{
    display: grid;
    grid-template-columns: 50% 50%;
    .left{
      .left-container{
        height: 300px;
        .hexagon-container{
          filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.6));
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          .hexagon{
            clip-path: polygon(50% 0%, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
            background: url(../../assets/img/c.jpeg) center/auto;
            width: 40%;
            height: 90%;
            overflow: hidden;
            box-shadow: 0px 5px 10px 0px $blue;
            transition: 0.5s;
            
              &:hover {
                transform: scale(1.1);
              }
          }
        }
      }
    }
    .right{
      .right-container{
        .content {
          .title {
            text-align: left;
            margin: 20px 0 10px 7%;
            font-family: $rosario-italic;
            font-size: 2rem;
            text-transform: uppercase;
          }
          .text {
            p {
              font-family: $rosario-italic;
              font-size: 1.3rem;
              text-align: justify;
              text-indent: 40px;
            }
          }
        }
      }
    }
  }
}