.section4{
  .top{
    margin-bottom: 50px;
    .top-content{
      text-align: center;
      margin: 80px 0 15px;
      .title{
        font-family: $rosario-regular;
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: 700;
        padding-bottom: 20px;
      }
      .subtitle{
        p{
          font-family: $rosario-italic;
        }
      }
    }
  }
  .bottom{
    .cards{
      margin: 100px 150px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .card{
        width: 300px;
        display: flex;
        flex-direction: column;
        align-items: center;
        .bottom{
          .title{
            text-align: center;
            margin-top: -20px;
            font-family: $rosario-italic;
            font-weight: 700;
            text-transform: uppercase;
            span{
              font-family: $charm;
              color: $red;
            }
          }
          p{
            font-family: $tangerine;
            font-size: 2rem;
            text-align: justify;
            text-indent: 40px;
            text-overflow : ellipsis;
          }
        }
      }
    }
  }
}