.section5{
  .section5-container{
    margin: 20px 400px;
    display: flex;
    align-items: center;
    justify-content: center;
    .left{
      .left-container{
        height: 500px;
        width: 380px;
        background: $red;
        .top{
          padding: 30% 0% 15% 10%;
          .title{
            font-family: $oswald;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 2.5rem;
            color: $beige;
          }
          .text{
            font-family: $rosario-italic;
            text-transform: capitalize;
            font-size: 1.3rem;
            font-weight: 300;
            color: $white;
            width: 80%;
            padding-top: 10px;
          }
        }
        .bottom{
          padding: 5% 30% 15% 10%;
          display: flex;
          justify-content: space-between;
          .left{
            .icone{
              font-size: 4rem;
              color: $beige;
            }
            .title{
              font-family: $rosario-italic;
              text-transform: capitalize;
              color: $white;
            }
          }
          .right{
            .icone{
              font-size: 4rem;
              color: $beige;
            }
            .title{
              font-family: $rosario-italic;
              text-transform: capitalize;
              color: $white;
            }
          }
        }
        
      }
    }
    .right{
      .right-container{
        height: 600px;
        width: 600px;
        background: $blue;
        margin-top: 0px;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .top{
          color: $beige;
          .title{
            font-family: $rosario-regular;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 2rem;
            margin-bottom: 20px;
          }
          p{
            font-family: $rosario-italic;
            font-size: 1.2rem;
          }
        }
        .bottom{
          width: 100%;
          .form{
            margin-top: 40px;
            .form-container{
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              font-family: $rosario-italic;
              .top{
                display: grid;
                grid-template-columns: 50% 50%;
                column-gap: 20px;
                width: 80%;
                align-items: center;
                justify-content: center;
                font-family: $rosario-italic;
                font-weight: 800;
                .left{
                display: flex;
                flex-direction: column;
                input {
                    height: 40px;
                    margin-bottom: 20px;
                    width: 100%;
                  }
                }
                .right{
                  display: flex;
                  flex-direction: column;
                  input {
                      height: 40px;
                      margin-bottom: 20px;
                      width: 100%;
                    }
                }
              }
              .bottom{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                width: 100%;
                color: $beige;
                font-weight: 800;
                textarea{
                  width: 84%;
                  height: 80px;
                  margin-bottom: 10px;
                }
                input{
                  padding: 5px 10px;
                  font-family: $rosario-italic;
                  border: 1px solid $red;
                  border-radius: 5px;
                  color: $blue;
                  background: $beige;
                  box-shadow: 0px 5px 10px 0px $blue;
                  width: fit-content;
                  transition: 2,5s;
                  cursor: pointer;
                  font-weight: 800;
                  &:hover{
                    transform: scale(1.09);
                    transition: 0.4s;
                    color: $red;
                    border: 1px solid $blue;
                    
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}